import React, { Component } from 'react';
import './index.css';

class Tax extends Component<{}, {taxin: number, taxout: number,tps: number, tvq: number}> {
    constructor (props:any){
        super(props);
        this.state = {
          taxin: 1,
          taxout: 1,
          tps: 0.05,
          tvq: 0.09975
        };
        this.handleChange = this.handleChange.bind(this);
        this.taxoutChange = this.taxoutChange.bind(this);
      }
    


      handleChange(e:any){
        var tps:number = (e.target.value * (this.state.tps));
        var tvq = (e.target.value * (this.state.tvq));
        var total:number = (parseFloat(e.target.value) + tps + tvq);
        total = total || 0;
        this.setState({
          taxin: +total.toPrecision(5),
          taxout: e.target.value
        });
      }
    
      taxoutChange(e:any){
        var tps = (e.target.value * (this.state.tps));
        var tvq = (e.target.value * (this.state.tvq));
        var total = (parseFloat(e.target.value) / 1.14975);
        total = total || 0
        this.setState({
          taxin: e.target.value,
          taxout: +total.toPrecision(5)
    
        });
      }
  
    
    render(){
        return(
        <>
         <header className="App-header">
          <label htmlFor="taxout">Sans taxes :</label>
          <input type="number" id="taxout" name="taxout" value={this.state.taxout} onChange={this.handleChange}></input>
          <label htmlFor="taxin">Tax In (TPS et TVQ):</label>
          <input type="number" id="taxin" name="taxin" value={this.state.taxin} onChange={this.taxoutChange}></input>
        </header>
        </>
        )
      }
    }

export default Tax;
